import { useState, useEffect, useContext, useCallback } from "react";
import { numberData } from "./mockData";
import BarChart from "@/Components/GraphAndChart/barChart";
import Template from "@/Components/GraphAndChartTemp/Template";
import { getTemplateData } from "@/Pages/Elderlies/Components/Utiles/utiles";
import { CustomContext } from "@/Context/UseCustomContext";
import dayjs from "dayjs";
import { getNumberOfBedExist } from "@/api/deviceReports";
export default function NumberOfBedExitStatistic({ isSummaryBtn = true }) {
  const { title, color, icon, summaryProps, dataAnalysis } =
    getTemplateData("Number of Bed Exit");
  const context = useContext(CustomContext);

  const {
    chartToDate = "",
    chartFromDate = "",
    elderlyDetails,
  } = context || {};
  const [toDate, setToDate] = useState(
    dayjs().subtract(1, "day").format("YYYY-MM-DD")
  );
  const [fromDate, setFromDate] = useState(
    dayjs().subtract(31, "day").format("YYYY-MM-DD")
  );
  const [numberOfBedExistStatistic, setNumberOfBedExistStatistic] = useState(
    []
  );
  const getNumberOfBedExistStatistic = useCallback(() => {
    getNumberOfBedExist({
      uids: elderlyDetails?.bedRoomIds,
      elderly_id: elderlyDetails._id,

      to_date: fromDate,
      from_date: toDate,
    })
      .then((res) => {
        setNumberOfBedExistStatistic(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [toDate, elderlyDetails]);
  useEffect(() => {
    getNumberOfBedExistStatistic();
  }, [getNumberOfBedExistStatistic]);
  useEffect(() => {
    if (chartToDate && chartFromDate) {
      setToDate(chartToDate);
      setFromDate(chartFromDate);
    }
  }, [chartToDate]);
  return (
    <Template
      title={title}
      color={color}
      icon={icon}
      dataAnalysis={dataAnalysis}
      isSummaryBtn={isSummaryBtn}
      summaryProps={summaryProps}
    >
      <BarChart
        data={numberOfBedExistStatistic}
        color={color}
        dataType="number"
        fromDate={fromDate}
        toDate={toDate}
        chartFor="numberOfBedExitTime"
      />
    </Template>
  );
}
